import { ethers } from "ethers";
import React, { useState, useEffect } from "react";
import abi from "../contractsData/MyNFTCollection.json";
import address from "../contractsData/MyNFTCollection-address.json";

const CreateSong = () => {
  const [contract, setContract] = useState(null);
  const [owner, setOwner] = useState("");
  const [tokenSupply, setTokenSupply] = useState(1);
  const [totalSupply, setTotalSupply] = useState(0);
  const [_uri, setUri] = useState("");
  const [totalFractionalAmount, setTotalFractionalAmount] = useState(0);

  useEffect(() => {
    const init = async () => {
      if (window.ethereum) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const contractAddress = address.address; // Ensure this is a valid Ethereum address
        const contractABI = abi.abi; // Ensure this is an array
        const contractInstance = new ethers.Contract(
          contractAddress,
          contractABI,
          signer
        );
        setContract(contractInstance);
      } else {
        alert("MetaMask not detected. Please install MetaMask.");
      }
    };

    init();
  }, []);

  const contractOwner = async () => {
    if (contract) {
      try {
        const owner = await contract.contractOwner(); // Ensure this function does not require arguments
        setOwner(owner);
        console.log(owner);
      } catch (error) {
        console.error("Error fetching contract owner:", error);
      }
    }
  };

  const _tokenSupply = async () => {
    if (contract) {
      try {
        const supply = await contract.tokenSupply(); // Ensure this function does not require arguments
        setTokenSupply(supply);
        console.log(supply);
      } catch (error) {
        console.error("Error fetching token supply:", error);
      }
    }
  };

  const _mint = async () => {
    if (contract) {
      try {
        const tx = await contract.mintNFT(_uri, totalSupply, totalFractionalAmount); // Pass correct arguments
        await tx.wait();
        console.log("Minted successfully");
      } catch (error) {
        console.error("Error minting token:", error);
      }
    }
  };

  return (
    <div>
      <h1>Create Song</h1>
      <button onClick={contractOwner}>Get Contract Owner</button>
      {owner && <p>Contract Owner: {owner}</p>}
      <button onClick={_tokenSupply}>Get tokenSupply</button>
      {<p>tokenSupply: {tokenSupply}</p>}
      <div>
        <div>
          <input
            type="number"
            value={totalSupply}
            onChange={(e) => setTotalSupply(e.target.value)}
            placeholder="totalSupply"
          />
          <input
            type="number"
            value={totalFractionalAmount}
            onChange={(e) => setTotalFractionalAmount(e.target.value)}
            placeholder="totalFractionalAmount"
          />
          <input
            type="text"
            value={_uri}
            onChange={(e) => setUri(e.target.value)}
            placeholder="uri"
          />
          <button onClick={_mint}>mint</button>
        </div>
      </div>
    </div>
  );
};

export default CreateSong;